  --brown-bg: #f0d1a5;
  --light-green: #c2e297;
  --mobile-menu-bg: #e17992;
  --red-title-text: #d41443;
  --pink-text: #ffa5ba;
  --light-brown-text: #907e82;
  --quotation-svg-color: #e1e1e1;
  --red-dot-color: #c7133e;
  --red-svg-home: #d31342;
  --testimonials-btn-color: #d9d9d9;
  --mobile-menu-border: #e18298;
  --white-color: #fff;
  --black-color: #000;
}

.hero-header {
  background-color: var(--pink-bg);
  justify-content: space-around;
  padding: 8px 20px 0;
  display: flex;
}

.hero-header-nav {
  display: none;
}

.hero-header-container {
  height: 502px;
  background-image: url("icecream.64ce6e41.png"), url("ellipse-header.7a740149.png");
  background-position: bottom, 50%;
  background-repeat: no-repeat;
  background-size: 175px, 400px;
  flex-direction: column;
  flex-basis: 100%;
  display: flex;
}

.hero-header-nav {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: -13px;
  display: flex;
}

.page-header_logo-container {
  width: 200px;
  height: 55px;
  flex: none;
  display: block;
}

.icon-header-logo {
  width: 100%;
  height: 100%;
}

.navigation-menu__list {
  color: var(--white-color);
  flex-direction: row;
  gap: 35px;
  padding: 0 10px;
  font-size: 16px;
  display: none;
}

.navigation-menu__list--item {
  justify-content: space-around;
  display: flex;
}

.navigation-menu__list--item:hover, .navigation-menu__list--item:focus {
  color: var(--red-title-text);
  cursor: pointer;
}

.menu-toggle {
  height: 25px;
  width: 15px;
  background-color: rgba(0, 0, 0, 0);
  border: rgba(0, 0, 0, 0);
  margin-left: auto;
  margin-right: 20px;
}

.menu-toggle:hover, .menu-toggle:focus {
  cursor: pointer;
}

.menu-toggle--svg {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
}

.navigation-menu__list--item {
  color: #d41443;
  align-items: center;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  display: flex;
}

.navigation-menu__list {
  color: #fff;
  align-items: center;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 21px;
  display: flex;
}

.buy-button {
  width: 160px;
  height: 44px;
  color: var(--red-title-text);
  background-color: var(--white-color);
  border: 1px rgba(0, 0, 0, 0);
  border-radius: 22px;
  justify-content: space-around;
  align-items: center;
  font-size: 16px;
  line-height: 21px;
  display: none;
}

.buy-button:hover, .buy-button:focus {
  cursor: pointer;
  background-color: #ecebeb;
}

.buy-button--svg {
  height: 10px;
  width: 8px;
}

.hero-header-hero-section {
  height: 100%;
  justify-content: space-between;
  display: flex;
}

.hero-header-left {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.header-title-container {
  width: 210px;
  height: 110px;
}

.hero-section--title, .form-title, .items-order__title, .hero-section--title_span {
  color: var(--white-color);
  text-transform: uppercase;
  font-family: Titan One, sans-serif;
  font-size: 26px;
  font-weight: 400;
  line-height: 29px;
}

.hero-section--title_span {
  color: var(--red-title-text);
  font-size: 48px;
  line-height: 46px;
}

.products-button {
  width: 62px;
  height: 24px;
  color: var(--white-color);
  background-color: var(--red-title-text);
  border: 1px rgba(0, 0, 0, 0);
  border-radius: 20px;
  font-size: 8px;
  line-height: 10px;
}

.products-button:hover, .products-button:focus {
  cursor: pointer;
}

.how-is-made-button {
  width: 82px;
  height: 24px;
  color: var(--red-title-text);
  background-color: var(--white-color);
  border: 1px rgba(0, 0, 0, 0);
  border-radius: 20px;
  font-size: 8px;
  line-height: 10px;
}

.how-is-made-button:hover, .how-is-made-button:focus {
  cursor: pointer;
  background-color: #ecebeb;
}

.bio-eco-holder {
  display: flex;
}

.text-holder {
  width: 115px;
  height: 50px;
}

.bio-eco-text {
  color: var(--white-color);
  font-size: 8px;
  font-weight: 400;
}

.button-bio-eco {
  width: 18px;
  height: 18px;
  background-color: var(--white-color);
  border: 1px rgba(0, 0, 0, 0);
  border-radius: 50%;
}

.button-bio-eco--svg {
  width: 100%;
  height: 100%;
  fill: var(--red-title-text);
  border-radius: 50%;
}

.bio-eco-image {
  width: 154px;
  height: 154px;
}

.counter__list--item {
  background-image: url("ellipseh@2x.d58e00e6.png");
  background-position: 100% 0;
  background-repeat: no-repeat;
  background-size: 20px;
  padding-top: 9px;
}

.counter__list--item-number {
  color: var(--red-title-text);
  font-family: Titan One, sans-serif;
  font-size: 28px;
  font-weight: 400;
  line-height: .9;
}

.counter__list--item-type {
  color: var(--white-color);
  font-size: 8px;
  font-weight: 400;
}

@media (max-width: 767px) {
  .display-none-header {
    display: none;
  }
}

@media (min-width: 768px) {
