  align-self: center;
  margin-top: 60px;
  margin-bottom: 20px;
}

.buy-button-mobile svg {
  margin-left: 15px;
}

.buy-button-mobile:hover svg, .buy-button-mobile:focus svg {
  transition: transform .2s ease-in;
  transform: translateX(10px);
}

.mobile-menu {
