}

.mobile-menu__item {
  width: 100%;
  height: 42px;
  border-top: 1px solid var(--quotation-svg-color);
  color: var(--white-color);
  align-items: center;
  font-weight: 700;
  line-height: 2;
  display: flex;
}

.mobile-menu__item a {
  margin-left: 20px;
}

.mobile-menu__item:last-child {
  border-bottom: 1px solid var(--quotation-svg-color);
}

